import React from 'react';
import { Card, Row, Col, Tabs, Icon, Button, Radio, Select,Table,Switch,Upload } from 'antd';
import {RequireUtils} from 'utils';
import createHistory from 'history/createHashHistory';
import { Divider } from 'antd';
import BasicMessage from './BasicMessage' ;
import AccountingMessage from './AccountingMessage' ;
import ShowCustomerDetailsTable from './ShowCustomerDetailsTable' ;
import ShowCustomerAddrDetailsTable from './ShowCustomerAddrDetailsTable'
import MyAvatar from './MyAvatar' ;
import mystyle from './tabstyle.less'
const TabPane = Tabs.TabPane;
const Option = Select.Option;

class ShowCustomerDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cust:{},
      addr:[],
      order:[],
      fileList:[],
      statistics:{}
    };
    this.callback = this.callback.bind(this);
  }
  callback(key) {
    console.log(key);
  }

  componentDidMount() {
    var _this = this ;
    var search = this.props.location.search ;
    var allparams = search.split("&") ;
    var id = allparams[0].split("=")[1] ;
    var myid = allparams[1].split("=")[1] ;
    RequireUtils.baseRequire("relation/cust-details",{cust_id:id},function (data) {
      if(data.code==1) {
        var list = data.data ;
        var mycust = list.cust ;
        mycust.myid = myid ;
        this.setState({
          cust:mycust,
          addr:list.addr,
          order:list.order,
          statistics:list.statistics,
          fileList:[{
            uid: -1,
            name: 'xxx.png',
            status: 'done',
            url: list.cust.license_url,
          }]
        })
      }else{
        this.setState({
          cust:{},
          addr:[],
          order:[],
          statistics:{}
        })
      }
    }.bind(this))
  }

  render() {
    const {cust,addr,order,fileList,statistics} = this.state ;
    const props = {
      action: '//jsonplaceholder.typicode.com/posts/',
      listType:"picture-card"
    };
    return (
      <div >
        <div style={{padding:'25px 15px'}}>
            <span style={{fontSize:'20px',fontWeight:'bold'}}></span>
        </div>
        <Card  type="card" >
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col style={{fontSize:'16px',color:'black',marginBottom:'10px'}}></Col>
          </Row>
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col span={18}>
              <BasicMessage cust={cust} ></BasicMessage>
            </Col>
            <Col span={6} className={'my-custom-upload'} style={{cursor:'pointer'}}>
              <Upload
                {...props}
                fileList={fileList}
                showUploadList={{showPreviewIcon:true,showRemoveIcon:false}}
              >
                {fileList.length >= 1 ? null : ""}
              </Upload>
              <br/>
              <div className={'my-text-center'}></div>
            </Col>
          </Row>
          <Divider></Divider>
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col style={{fontSize:'16px',color:'black',marginBottom:'10px'}}></Col>
          </Row>
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col span={18}>
              <AccountingMessage cust={cust} statistics={statistics}></AccountingMessage>
            </Col>
            <Col span={6}>
            </Col>
          </Row>
          <Divider></Divider>
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col style={{fontSize:'16px',color:'black',marginBottom:'10px'}}>
              <div>
                <span></span>
                <span style={{float:'right',marginRight:'10px'}}>
                  <span style={{marginRight:'10px'}}><span style={{fontSize:'14px',fontWeight:100}}>&nbsp;</span><span className={'my-customer-color'}>{statistics.total_money}</span></span>
                  <span><span style={{fontSize:'14px',fontWeight:100}}>&nbsp;</span><span className={'my-customer-color'}>{statistics.total_num}</span></span>
                </span>
              </div>
            </Col>
          </Row>
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col><ShowCustomerDetailsTable order={order}></ShowCustomerDetailsTable></Col>
          </Row>
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col style={{fontSize:'16px',color:'black',marginBottom:'10px'}}></Col>
          </Row>
          <Row gutter={8} style={{ marginTop: '10px' }}>
            <Col>
              <ShowCustomerAddrDetailsTable addr={addr}></ShowCustomerAddrDetailsTable>
            </Col>
          </Row>
        </Card>
      </div >
    )
  }
}
export default ShowCustomerDetails;



